.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar{
  display : flex;
  height: 50px;
  background-color: #00c73c;
  /* text-align : start;*/
  padding-bottom: 50px;
  padding-left : 20px; 
  width: 100%;
  font-size : 20px;
  color: white;
  font-weight: bolder;
}

div{
  box-sizing :content-box;
}
.list{
  padding-left : 20px;
  /* display : flex; */
  text-align: left;
  border-bottom: 1px solid gainsboro;
}

.modalContent{
  width : 300px;
  height: 350px;
  background-color: #00c73c;
  
  top : 50%;
  left : 50%;
  position: fixed;
  transform: translate(-50%, -50%);
}